import {
    USER_LOGIN_REQUEST,
    USER_LOGIN_SUCCESS,
    USER_LOGIN_FAIL,
    USER_LOGOUT,

    USER_REGISTER_FAIL,
    USER_REGISTER_REQUEST,
    USER_REGISTER_SUCCESS,

    USER_DETAILS_SUCCESS,
    USER_DETAILS_REQUEST,
    USER_DETAILS_RESET,
    USER_DETAILS_FAIL,

    USER_UPDATE_PROFILE_SUCCESS,
    USER_UPDATE_PROFILE_REQUEST,
    USER_UPDATE_PROFILE_FAIL,

    USER_UPDATE_SUCCESS,
    USER_UPDATE_REQUEST,
    USER_UPDATE_FAIL,

    USER_DELETE_FAIL,
    USER_DELETE_SUCCESS,
    USER_DELETE_REQUEST,

    USER_LIST_FAIL,
    USER_LIST_REQUEST,
    USER_LIST_SUCCESS,
    USER_LIST_RESET,
} from "../constants/userConstants";
import { ORDER_MYLIST_RESET } from "../constants/orderConstants";
import axios from "axios";

export const login = (email, password) => async (dispatch, getState) => {
    try{
        dispatch({
            type: USER_LOGIN_REQUEST,
        });
        const config = {headers: {'content-type': 'application/json'}}
        const { data } = await axios.post(
                        `/users/login/`,
                        {'username': email, 'password': password},
                        config);
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data,
        });
        localStorage.setItem('userInfo', JSON.stringify(data));
    }
    catch(error){
        dispatch({
            type: USER_LOGIN_FAIL,
            payload:
              error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message
        });
    }
};

export const logout = () => (dispatch) => {
    localStorage.removeItem('userInfo');
    dispatch({ type: USER_LOGOUT, });
    dispatch({ type: USER_DETAILS_RESET });
    dispatch({ type: ORDER_MYLIST_RESET });
    dispatch({ type: USER_LIST_RESET });
};

export const register = (name, email, password) => async (dispatch, getState) => {
    try{
        dispatch({
            type: USER_REGISTER_REQUEST,
        });
        const config = {headers: {'content-type': 'application/json'}}
        const { data } = await axios.post(
                        `/users/register/`,
                        {'name': name,'email': email, 'password': password},
                        config);
        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data,
        });
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data,
        });
        localStorage.setItem('userInfo', JSON.stringify(data));
    }
    catch(error){
        dispatch({
            type: USER_REGISTER_FAIL,
            payload:
              error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message
        });
    }
};

export const userDetailsUpdate = (user,id) => async (dispatch,getState) => {
  try {
    dispatch({ type: USER_UPDATE_PROFILE_REQUEST });
    const { userLogin: {userInfo},} = getState();
    const config = {
        headers: {
            'content-type': 'application/json',
             Authorization: `Bearer ${userInfo.token}`,
        }
    }
    const { data } = await axios.put(`/users/profile/update`,user,config);
    dispatch({ type: USER_UPDATE_PROFILE_SUCCESS, payload: data });

    dispatch({ type: USER_LOGIN_SUCCESS, payload: data, });
    localStorage.setItem('userInfo', JSON.stringify(data));

  } catch (error) {
    dispatch({
      type: USER_UPDATE_PROFILE_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail : error.message
    });
  }
};

export const userUpdateById = (user) => async (dispatch,getState) => {
  try {
    dispatch({ type: USER_UPDATE_REQUEST });
    const { userLogin: {userInfo},} = getState();
    const config = {
        headers: {
            'content-type': 'application/json',
             Authorization: `Bearer ${userInfo.token}`,
        }
    }
    const { data } = await axios.put(`/users/update/${user.id}/`,user,config);
    dispatch({ type: USER_UPDATE_SUCCESS});
    dispatch({ type: USER_DETAILS_SUCCESS, payload: data });

  } catch (error) {
    dispatch({
      type: USER_UPDATE_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail : error.message
    });
  }
};

export const userDetails = (id) => async (dispatch,getState) => {
  try {
    dispatch({ type: USER_DETAILS_REQUEST });
    const {userLogin: {userInfo},} = getState();
    const config = {
        headers: {
            'content-type': 'application/json',
            Authorization: `Bearer ${userInfo.token}`,
        }
    }
    id = id === 'profile' ? 'profile' : id.id;
    const { data } = await axios.get(`/users/${id}/`,config);

    dispatch({ type: USER_DETAILS_SUCCESS, payload: data });
  } catch (error) {
    dispatch({
      type: USER_DETAILS_FAIL,
      payload:
        error.response && error.response.data.detail
          ? error.response.data.detail : error.message
    });
  }
};

